// InfantDoseTimingAnalysis.tsx
import React from "react";

type VaccineSchedule = {
  id: string;
  firstDose: string;
  secondDose: string;
  thirdDose: string;
  UpdateFirstDose: string;
  UpdateSecondDose: string;
  UpdateThirdDose: string;
  remark_FirstDose?: string;
  remark_SecondDose?: string;
  remark_ThirdDose?: string;
};

type InfantData = {
  id: string;
  fullname: string;
  // other infant properties…
  Vaccination_Schedule: VaccineSchedule[];
};

const MS_PER_DAY = 1000 * 60 * 60 * 24;

/**
 * Calculates the difference in days between the scheduled date and the actual (updated) date.
 * A positive number means the vaccine was administered early,
 * while a negative number means it was administered late.
 */
function calculateDayDifference(
  scheduledDate: string,
  updatedDate: string
): number {
  const scheduled = new Date(scheduledDate);
  const updated = new Date(updatedDate);
  // Divide the difference by milliseconds per day and round to the nearest day.
  return Math.round((scheduled.getTime() - updated.getTime()) / MS_PER_DAY);
}

/**
 * Given a scheduled date and an updated date, returns a predictive analysis message.
 */
function getDoseTimingMessage(
  scheduledDate: string,
  updatedDate: string,
  doseName: string
): string {
  const diffDays = calculateDayDifference(scheduledDate, updatedDate);
  let message = `${doseName}: `;

  if (diffDays > 0) {
    // Administered early
    message += `Administered ${diffDays} day(s) early. `;
    message +=
      "Vaccination given before the recommended date might lead to a suboptimal immune response because the child's immune system may not be fully prepared. A booster might be needed later.";
  } else if (diffDays < 0) {
    // Administered late
    message += `Administered ${Math.abs(diffDays)} day(s) late. `;
    message +=
      "A delayed vaccination prolongs the window of vulnerability, which could increase the risk of infection during the delay.";
  } else {
    // Administered on time
    message +=
      "Administered on time. This is optimal for developing a robust immune response.";
  }

  return message;
}

/**
 * This component uses the infant data returned from the API to provide a dose timing analysis
 * for each vaccination schedule.
 */
export default function InfantDoseTimingAnalysis({
  infantData,
}: {
  infantData: InfantData;
}) {
  const schedules = infantData.Vaccination_Schedule;

  if (!schedules || schedules.length === 0) {
    return (
      <div className="bg-white p-4 rounded shadow-md my-4">
        <h3 className="font-bold text-xl mb-4">
          Dose Timing Predictive Analysis
        </h3>
        <p>No vaccination schedules available for analysis.</p>
      </div>
    );
  }

  return (
    <div className="bg-white p-4 rounded shadow-md my-4">
      <h3 className="font-bold text-xl mb-4">
        Dose Timing Predictive Analysis
      </h3>
      {schedules.map((schedule, index) => (
        <div key={schedule.id} className="mb-6 border-b pb-4">
          <h4 className="text-lg font-semibold mb-2">Schedule {index + 1}</h4>
          <p>
            {getDoseTimingMessage(
              schedule.firstDose,
              schedule.UpdateFirstDose,
              "First Dose"
            )}
          </p>
          <p className="mt-2">
            {getDoseTimingMessage(
              schedule.secondDose,
              schedule.UpdateSecondDose,
              "Second Dose"
            )}
          </p>
          <p className="mt-2">
            {getDoseTimingMessage(
              schedule.thirdDose,
              schedule.UpdateThirdDose,
              "Third Dose"
            )}
          </p>
        </div>
      ))}
    </div>
  );
}
